<template>
<div class="listMain">
	<h6 class="allTitle">文章</h6>
    <ul class="articleSort" @click="chooseLi">
        <li>排序:</li>
        <li class="sortSelected">全部</li>
        <li>最新发布</li>
        <li>评论最多</li>
        <li>人气最高</li>
        <li>收藏最多</li>
    </ul>
    <div class="titleBox"  v-for="n in num">
        <div class="detailBox">
            <router-link to="/articleDetail">
                <img src="" alt="" class="detailsImg">
            </router-link>
            <div class="contentBox">
            <router-link to="/articleDetail">
                <p class="contentTitle"><span>【HTML】</span><span style="color:#000">《新年这一刻》MV1</span></p>
            </router-link>
                <p class="contentPreson">
                    <span class='textColor'>发布作者名字</span>
                    <span>/</span>
                    <span>1小时前上传</span>
                    <span>/</span>
                    <span class="icon-thumbs-up">278</span>
                    <span>/</span>
                    <span class="icon-comment-alt">12</span>
                    <span>/</span>
                    <span class="icon-share-alt">49</span>
                </p>
                <p class="contentdepicts">重返政坛？英前首相卡梅伦或将成为北约秘书长</p>
            </div>
        </div>
    </div>
    
</div>
</template>
<script type="text/javascript">
    import '../../assets/font/css/font-awesome.min.css'
    import  $ from  'jquery'
	export default{
		data(){
	       	  return{
                num:10
             }
		},
        methods:{
            chooseLi(ev){
                if($(ev.target).index()){      
                    $(ev.target).parent().children().removeClass("sortSelected");
                    $(ev.target).addClass("sortSelected");
                }

            }
        }
	}
</script>
<style type="text/css">
	.listMain{
		float: right;
		width: 85%;
		background-color: #fff;
	}
    .allTitle{
        margin-top: 20px;
        font-size: 35px;
        text-align: center;
    }
    .articleSort{
        width:87.5%;
        height: 24px;
        margin-left: 12.5%;
        margin-top: 20px;
    }
    .articleSort>li{
        width:14.5%;
        height: 24px;
        display: inline-block;
        text-align: center;
        line-height: 24px;
        color: #999;
        font-size: 16px;
        cursor: default;
    }
    .sortSelected{
        background: #405CA3;
        color:white !important;
    }
    .titleBox{
        width:95%;
        margin-top: 20px;
        margin-left: 5%;
    }
    .detailBox{
        width:95%;
        height:150px;
        border-top: 1px solid #bbb;
    }
    .detailsImg{
        width:20%;
        height:110px;
        margin:20px 5% 20px 5%;
        border:0;
        background:#c9c9c9;
        display:inline-block;
    }
    .contentBox{
        display:inline-block;
        width:68%;
        height:110px;
        vertical-align: top;
        background:white;
    }
    .contentTitle{
        width:100%;
        height:36px;
        line-height: 36px;
        margin-top:20px ;
        font-size: 18px;
        color:'';
    }
    .contentPreson{
        width:100%;
        height:36px;
        line-height: 36px;
        font-size: 14px;
        text-indent: 8px;
        color:#999;
    }
    .contentdepicts{
        width:100%;
        height:36px;
        line-height: 36px;
        font-size: 16px;
        text-indent: 8px;
        color:#999;
    }
    .textColor{
        color:#835b5b;
    }
</style>


